.user-profile-main-container {
    display: flex;
    height: 100vh;

    & .user-profile-panel {
        flex-grow: 1;
        overflow-y: auto;
        height: 100vh;
        @include scrollbars(8px, #afafaf);

        & .user-profile-header {
            padding: 25px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: $blue;
            height: 300px;
            text-align: center;

            & > a {
                align-self: flex-start;

                & > svg {
                    fill: $white;
                    font-size: 2.5rem;
                }
            }

            & .user-profile-initials {
                background-color: $lightblue;
                color: $white;
                width: 100px;
                min-height: 100px;
                overflow: hidden;
                line-height: 100px;
                border-radius: 50%;
                font-size: 3.5rem;
                margin-bottom: 25px;
                user-select: none;

                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            & h3 {
                color: $white;
                letter-spacing: 3px;
                font-size: 1.8rem;
                font-weight: 400;
                margin-bottom: 25px;
            }

            & .user-profile-tabs {
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                & a {
                    color: $white;
                    margin: 0 5px;
                    font-size: 0.9rem;
                    transition: box-shadow 0.2s ease;

                    &:hover {
                        box-shadow: 0px 5px 0px 0px rgba(255, 255, 255, 1);
                    }
                }

                margin-bottom: 10px;
            }
        }
    }
}

.user-profile-content {
    & .general-container {
        height: 100%;
        & .general-inner-container {
            margin: auto;
            max-width: 1200px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 40px;
            height: 100%;
            padding: 20px;
            justify-content: center;
            & .list-container {
                border: solid 3px $lightgrey;
                padding: 20px;
                max-width: 350px;
                flex-grow: 1;
                flex-basis: 100%;
                height: 350px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                & .list-title-container {
                    display: flex;
                    justify-content: space-between;
                    & > h2 {
                        font-size: 1.5rem;
                        font-weight: 400;
                    }

                    & > svg {
                        font-size: 2rem;
                    }
                }

                & .list-items-container {
                    color: $black;
                    overflow-y: auto;
                    padding: 10px;
                    background-color: $white;
                    flex-grow: 1;
                    margin-top: 10px;
                    @include scrollbars(8px, $grey);
                    width: 100%;

                    & .info-row {
                        &:not(:last-child) {
                            margin-bottom: 10px;
                        }
                        & a {
                            padding: 0 5px;
                            min-height: 40px;
                            color: $black;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            transition: all 0.2s ease;

                            & span {
                                font-size: 1rem;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                            }

                            &:hover {
                                background-color: $lightgrey;

                                & svg {
                                    color: $blue;
                                }
                            }

                            & svg {
                                font-size: 2rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

.update-profile-container {
    & .update-profile-inner-container {
        max-width: 350px;
        margin: 25px auto;
        padding: 40px 20px;
        // background-color: #ffffff;
        border: 3px solid $lightgrey;

        & > form {
            & > div:not(:last-child) {
                margin-bottom: 20px;
                display: flex;
                flex-direction: column;
            }
            & > div:not(:first-child) {
                & label {
                    font-size: 0.9rem;
                    margin-bottom: 5px;
                }
                & input {
                    border: none;
                    padding: 5px;
                    border-bottom: 1px solid $black;
                    background-color: rgba(255, 255, 255, 0);
                    transition: all 0.2s ease;

                    &:focus {
                        background-color: #eeeeee;
                    }

                    &[type="date"] {
                        cursor: pointer;
                    }
                }
            }

            & img {
                object-fit: cover;
            }

            & .user-thumbnail {
                position: relative;
                display: inline-block;
                font-size: 3rem;
                width: 125px;
                height: 125px;
                line-height: 125px;
                cursor: pointer;
                transition: all 0.2s ease;
                margin: auto;

                &:hover {
                    filter: brightness(50%);

                    &h3 {
                        opacity: 1;
                    }
                }

                &.loading-img {
                    filter: brightness(50%);
                }
            }
            .loader-img-container {
                width: 70%;
            }

            & > div:first-child {
                height: 100px;
                margin-bottom: 40px;

                & .user-hover {
                    &:hover {
                        & h3 {
                            opacity: 1;
                        }
                    }
                }

                & h3 {
                    position: absolute;
                    top: 75%;
                    color: $white;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    opacity: 0;
                    transition: opacity 0.2s ease;

                    &.loading {
                        opacity: 1;
                    }
                }
                & label {
                    margin: auto;
                }

                & input {
                    display: none;
                }
            }

            & button {
                background-color: $blue;
                transition: background 0.2s ease;
                display: block;
                border: none;
                color: $white;
                font-weight: 700;
                font-size: 1.2rem;
                padding: 5px 10px;
                border-radius: 50px;
                margin: 25px auto 0;
                &:hover {
                    background-color: #3d9ffa;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .user-profile-main-container {
        display: block;
        & .user-profile-panel {
            height: calc(100vh - 65px);
        }
    }
    .sunday-tooltip {
        display: none !important;
    }
}
